import React from "react";
import { BasicButton } from "..";
import { useHistory } from "react-router-dom";

function BackButton(props) {
  let history = useHistory();
  return (
    <BasicButton
      do={() => {
        if (props.back) {
          window.location.assign(props.back);
        } else {
          history.goBack();
        }
      }}
      text={props.text || ""}
    />
  );
}
export default BackButton;



