import React from "react";
import {
  BodyWrapper,
  GiftFooter,
  Logo,
  Hamburger,
  Message,
  GiftImage,
} from "..";

function GiftBody(props) {
  return (
    <BodyWrapper>
      <Logo />
      <Hamburger />
      <Message contents=" !"/>
      <GiftImage />
      <GiftFooter back='/' next={props.next} nextText='' />
    </BodyWrapper>
  );
}

export default GiftBody;



